    0%{ -webkit-transform: translate(2%, -2%); transform: translate(2%, -2%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }
    10%{ -webkit-transform: translate(-6%, 6%); transform: translate(-6%, 6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }

    20%{ -webkit-transform: translate(6%, -6%); transform: translate(6%, -6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }
    30%{ -webkit-transform: translate(-6%, 6%); transform: translate(-6%, 6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }

    40%{ -webkit-transform: translate(6%, -6%); transform: translate(6%, -6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual);}
    50%{ -webkit-transform: translate(-6%, 6%); transform: translate(-6%, 6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }

    100%{ top: 0; top: var(--topOriginal);  left: 0;  left: var(--leftOriginal); }
}


@keyframes tremida{
    0%{ -webkit-transform: translate(2%, -2%); transform: translate(2%, -2%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }
    10%{ -webkit-transform: translate(-6%, 6%); transform: translate(-6%, 6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }

    20%{ -webkit-transform: translate(6%, -6%); transform: translate(6%, -6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }
    30%{ -webkit-transform: translate(-6%, 6%); transform: translate(-6%, 6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }

    40%{ -webkit-transform: translate(6%, -6%); transform: translate(6%, -6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual);}
    50%{ -webkit-transform: translate(-6%, 6%); transform: translate(-6%, 6%); top: 0; top: var(--topAtual); left: 0; left: var(--leftAtual); }

    100%{ top: 0; top: var(--topOriginal);  left: 0;  left: var(--leftOriginal); }
}

