/* EFEITO PISCAR QUE OCORRE QUANDO AS PEÇAS SÃO GIRADAS PARA A DIREÇÃO CORRETA. 
ELE OCORRE NO ESPAÇO CENTRAL VAZIO QUE ESTÁ RODEADO DAS PEÇAS GIRATÓRIAS, FAZENDO APARECER 
RAPIDAMENTE A IMAGEM COMPLETA DE UM CAVALO OU DO PASSARO. */
@-webkit-keyframes piscar{
    0%{ opacity: 0.25; }
    25%{ opacity: 0.5; }
    50%{ opacity: 1.0; }
    75%{ opacity: 0.5; }
    100%{ opacity: 0.25; }
}
@keyframes piscar{
    0%{ opacity: 0.25; }
    25%{ opacity: 0.5; }
    50%{ opacity: 1.0; }
    75%{ opacity: 0.5; }
    100%{ opacity: 0.25; }
}

